.result-list-loading-item {
  width: 100%;
  height: 100px;
  background-color: #f3f3f3;
  background: -webkit-gradient(linear, left top, right top, color-stop(5%, #f3f3f3), color-stop(20%, #eee), color-stop(35%, #f3f3f3));
  background: -webkit-linear-gradient(left, #f3f3f3 5%, #eee 20%, #f3f3f3 35%);
  background: -o-linear-gradient(left, #f3f3f3 5%, #eee 20%, #f3f3f3 35%);
  background: linear-gradient(to right, #f3f3f3 5%, #eee 20%, #f3f3f3 35%);
  -webkit-animation: placeholderShimmer 2s linear 0s infinite normal forwards;
          animation: placeholderShimmer 2s linear 0s infinite normal forwards;
  border-radius: 6px;
  margin-bottom: 24px;
  opacity: 0;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.result-list-loading-item.show {
  opacity: 1;
}

@-webkit-keyframes placeholderShimmer{
    0%{
        background-position: -500px 0
    }
    100%{
        background-position: 500px 0
    }
}

@keyframes placeholderShimmer{
    0%{
        background-position: -500px 0
    }
    100%{
        background-position: 500px 0
    }
}


