.search-box {
  width: 100%;
  margin: 24px 0;
}

.search-form {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.search-form input {
  width: 100%;
  height: 42px;
  background: #fff;
  padding: 8px 45px 8px 24px;
  outline: none;
  border-radius: 2px;
  overflow: none;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  border: 1px solid rgba(50,50,93,.1);
  -webkit-box-shadow: 0 1px 2px 0 rgba(50,50,93,.1);
          box-shadow: 0 1px 2px 0 rgba(50,50,93,.1);
}

.search-form input:hover,
.search-form input:focus {
  -webkit-box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
          box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}

.search-form button {
  width: 42px;
  height: 42px;
  position: absolute;
  top: 0;
  right: 0;
}

.search-form:hover .icon.search,
.search-form:focus .icon.search {
  color: #333;
}


