.header {
  width: 100%;
  height: 60px;
}

.header .container {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.brand a {
  font-family: 'Montserrat', Arial, sans-serif;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
  text-transform: uppercase;
}

.brand {
  opacity: 1;
  -webkit-transition: opacity .3s ease;
  -o-transition: opacity .3s ease;
  transition: opacity .3s ease;
}

.header.initialized .brand,
.header.initialized .social {
  opacity: 0;
}

.social a img {
  width: 24px;
  -o-object-fit: cover;
     object-fit: cover;
}


