/* Icons from https://cssicon.space/ */
.icon-wrapper {
  width: 24px;
  height: 27px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  padding: 4px;
}

.search.icon {
  color: #3F3E43;
  position: absolute;
  width: 14px;
  height: 14px;
  border: solid 2px currentColor;
  border-radius: 100%;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}
.search.icon:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 4px;
  height: 10px;
  width: 2px;
  background-color: currentColor;
}


