.hero {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  height: 150px;
  background: #f5f5f5;
}

.hero > .container {
  -ms-flex-positive: 2;
      flex-grow: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.hero.initialized {
  height: 100vh;
}

.hero.success {
  background: #417AFF;
}

.quote-box {
  color: #333;
  font-size: 16px;
  font-family: 'Montserrat', Arial, sans-serif;
  padding: 24px 10%;
}

.quote-box p:first-child {
  text-align: center;
}

.quote-box p:last-child {
  text-align: right;
  font-weight: 600;
  letter-spacing: 1px;
}

.hero-brand {
  text-align: center;
}

.hero-brand a {
  color: #333;
  font-family: 'Montserrat', Arial, sans-serif;
  font-size: 60px;
  letter-spacing: 1px;
  font-weight: 600;
}



