.pagination {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 24px 0;
}

.pagination-wrapper {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.pagination-item span {
  font-size: 16px;
  font-weight: 600;
  display: block;
  width: 100%;
  height: 100%;
  padding: 16px;
  cursor: pointer;
  font-family: 'Montserrat', Arial, sans-serif;
}

.pagination-item span:hover {
  border-radius: 2px;
  overflow: hidden;
  color: #0984FF;
}

.pagination-item.is-active span {
  color: #0984FF;
}

.pagination-item.is-active span {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: initial;
}


